

























































































































import uniSegmentedControl from '../../../components/uni-segmented-control.vue';
import uniPopup from '../../../components/uni-popup-bank.vue';
export default {
	components: {
		uniSegmentedControl,uniPopup
	},
	data() {
		return {
			items: [
				'微信支付',
				'支付宝',
			],
			vipInfo:{
				payWay:1,
				nickname:'',
				viptime:'',
				viptq:'',
				vippayff:'第一步:长按或截图二维码图片进行保存;<br />第二步:打开支付宝或微信，点开扫一扫;<br />第三步:点右上角从相册中选择刚保存的二维码;<br />第四步:输入购买等级资费的相应金额;<br />第五步:支付时添加备注，备注会员手机号;<br />第六步:支付完成后，等待系统充值;',
				vipmoney:[
					{
						title:'',
						val:'',
						id:'',
					},
					{
						title:'',
						val:'',
						id:'',
					},
					{
						title:'',
						val:'',
						id:'',
					}
				],
				alipay:'',
				weixin:''
			},
			current: 0,
			activeColor: '#fe5950',
			styleType: 'text',
			vipMoney:0,
			credit_name:'',
			credit_id:'',
			loading:false,
			disabled:true,
			popType: 'middle',
			showPopupBottom: false,
			radioItems:[],
			group_id:0,
		}
	},
	onLoad() {
		
	},
	onShow() {
		this.getVipInfo();
	},
	onHide() {
		if (this.showPopupBottom) {
			this.showPopupBottom = false;
		}
	},
	onBackPress() {			
		if (this.showPopupBottom) {
			this.showPopupBottom = false;
			return true;
		}
	},
	onUnload() {			
		if (this.showPopupBottom) {
			this.showPopupBottom = false;
		}
	},
	methods: {
		payAgentFee(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const group_id = this.group_id;
			const credit_id = this.credit_id;
			if(openid == null){
				return;
			}
			
			if(credit_id == ''){
				uni.showModal({
					content: '请选择付款账户',
					showCancel: false
				});
				return;
			}
			uni.showModal({ //提醒用户更新
				title: '提示',
				content: '您确定支付相应金额，进行升级吗？',
				success: (res) => {
					if (res.confirm) {				
						const that = this;
						try {
							if(this.loading){
								return;
							}
							this.loading = true;
							this.$http.post(this.websiteUrl+'/api/agent/payAgentFee',{openid,sessionKey,group_id,credit_id},(res) => {
								if(res.data.code==1){
									if(res.data.result.url != ''){
										that.loading = false;
										uni.navigateTo({
											url: '/pages/card/web-view/web-view?url='+res.data.result.url
										})
									}else{
										uni.showModal({
											content:res.data.msg,
											showCancel:false,
											success:function() {
												that.loading = false;
												uni.navigateBack();
											}
										})
									}
								}else{
									uni.showModal({
										content:res.data.msg,
										showCancel:false,
										success:function() {
											that.loading = false;
										}
									})
								}
							});
						} catch (e) {
							uni.showModal({
								content:'网络异常,请稍后重试...',
								showCancel:false,
								success:function() {
									that.loading = false;
								}
							})
						}
						
					}
				}
			});
		
		},
		radioChange: function(e) {
			var checked = e.target.value
			var changed = {}
			for (var i = 0; i < this.radioItems.length; i++) {
				if (checked.indexOf(this.radioItems[i].name) !== -1) {
					changed['radioItems[' + i + '].checked'] = true;
					this.credit_name = this.radioItems[i].value;
				} else {
					changed['radioItems[' + i + '].checked'] = false;
				}
			}
			this.credit_id = e.detail.value;
			this.showPopupBottom = false;
		},
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupBottom = false;
			this.showAisleBottom = false;
		},
		//展示底部 popup
		showBottomPopup: function() {
			this.hidePopup();
			this.popType = 'bottom';
			this.showPopupBottom = true;
		},
		goDetailPage(url) {
			const openid = this.$store.state.openid;
			if(openid == null){
				uni.navigateTo({
					url: '/pages/user/login/login'
				})
				return false;
			}
			uni.navigateTo({
				url: url
			})
		},
		getVipInfo(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			try {
				this.$http.post(this.websiteUrl+'/api/agent/getAgentInfo',{openid,sessionKey},(res) => {
					if(res.data.code==1){
						this.vipInfo = res.data.result;
						this.radioItems = res.data.result.creditCard;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
		},
		onClickNum(num,id){
			this.vipMoney = num;
			this.group_id = id;
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		}
	}
}
